body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.header {
  background-color: rgb(2, 23, 53);
  display: block;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  padding-left: 16px;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App-table {
  display:block;
  font-size: calc(10px + 2vmin);
  color: white;
}
.row1{
  display: block;
  background-color: rgb(2, 23, 53);
}

.row2{
  z-index: 5;
  width: 99%;
  display: block;
}
.buttonsearch{
  position: absolute;
  margin-top: 200px;
  
  z-index: 5;
  width: 95%;
  text-align: center;
}
.Button1{
  display: inline;
  font-size: 18px;
}
.Button2{
  margin-left: 10%;
  display: inline;
  font-size: 18px;
}

.footer{
  font-size: 25px ;
  float:left;
  display:inline;
}
.footer1{
  float:left;
  display:inline;
  font-size: 20px;
  margin-left: 30%;
}
.linkfooter{
  text-decoration: none;
  color: white;
  margin-left: 2%;
  margin-top: 5px;
  float: left;
  display: inline;
}